.app {
  display: flex;
  position: relative;
}
#root, .app {
  height: 100%;
}
.app main {
  padding: 24px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}
.img-profile {
  max-width: 8.5rem;
  max-height: 8.5rem;
  border: .5rem solid hsla(0,0%,100%,.2);
}
.social-icons .social-icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 3.5rem;
  width: 3.5rem;
  background-color: #495057;
  color: #fff;
  border-radius: 100%;
  font-size: 1.5rem;
  margin-right: 1.5rem;
}
section.resume-section {
  display: flex;
  align-items: center;
  padding: 2rem 1rem;
  max-width: 75rem;
}
@media (min-width: 992px){
  section.resume-section {
      padding: 2rem 1rem;
  }
}
@media (min-width: 768px){
  section.resume-section {
      min-height: 60vh;
  }
}
@media (min-width: 992px){
  #sideNav {
      text-align: left;
      position: fixed;
      top: 0;
      left: 0;
      display: flex;
      flex-direction: column;
      width: 17rem;
      height: 100vh;
  }
}
@media (min-width: 992px){
  .navbar-expand-lg {
      flex-flow: row nowrap;
      justify-content: flex-start;
  }
}
.fixed-top {
  position: fixed;
  right: 0;
  left: 0;
  z-index: 1030;
}
.subheading {
  font-family: "Saira Extra Condensed",-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
  text-transform: uppercase;
  font-weight: 500;
  font-size: 1.5rem;
}
.dev-icons {
  font-size: 3rem;
}
.list-inline, .list-unstyled {
  padding-left: 0;
  list-style: none;
}
dl, ol, ul {
  margin-top: 0;
}
address, dl, ol, ul {
  margin-bottom: 1rem;
}
.mb-5, .my-5 {
  margin-bottom: 3rem!important;
}
.mb-0, .my-0 {
  margin-bottom: 0!important;
}

.app .btn-toggle {
  cursor: pointer;
  width: 35px;
  height: 35px;
  background: #353535;
  color: #fff;
  text-align: center;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  display: none;
}
@media (max-width: 768px){
  .app .btn-toggle {
      display: flex;
  }
}
.flex-grow-1 {
  flex-grow: 1!important;
}
@media (min-width: 768px){
  .flex-md-row {
      flex-direction: row!important;
  }
}
.flex-shrink-0 {
  flex-shrink: 0!important;
}
section.resume-section .resume-section-content {
  width: 100%;
}
.app main footer {
  margin-top: auto;
  color: #888;
  text-align: center;
}
.app .card{
  border: none;
  border-radius: 0px;
  -webkit-box-shadow: 0 4px 5px 0 rgba(0,0,0,0.14), 0 1px 10px 0 rgba(0,0,0,0.12), 0 2px 4px -1px rgba(0,0,0,0.3);
  box-shadow: 0 4px 5px 0 rgba(0,0,0,0.14), 0 1px 10px 0 rgba(0,0,0,0.12), 0 2px 4px -1px rgba(0,0,0,0.3);
}
.pro-sidebar .pro-menu a.active{
  font-weight: 500;
  color: #dce8ef;
}
.img-section{
  text-align: center;
  padding: 0 24px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  letter-spacing: 1px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}